function clearFiles() {
	file_list.value.data = []
}


// save_files() é chamada quando o evento on_change do botão 
// Choose Files é disparado.
function save_files(new_file)  {
	if (new_file) {	
		// test if new_file exists in file_list 
		const exist = file_list.value.data.some( element => {
			if (element.filename == new_file.name) {
				return true;
			} else {
				return false;
			}
		})
		// se nao exist
		if (!exist) {
			// geração do token de cancelamento
			const CancelToken = axios.CancelToken
			const source = CancelToken.source();
			// inserção do arquivo em file_list
			file_list.value.data.push({
							filename: new_file.name,    		 // FILE NAME
							file: 		new_file,							 // FILE RAW
							size: 		new_file.size,				 // FILE SIZE
							progress: '0%',									 // UPLOAD PROGRESS
							upload_status: 'empty',					 // UPLOAD STATUS
							upload_finish: false,						 // UPLOAD FINISH
							cancelSource: source,						 // CANCEL TOKEN
						  cancelled: false,								 // IS CANCELLED 
			});
			total_files_to_upload.value = file_list.value.data.length;
		}
	}
}



function upload_file_list() {
	// set upload_on
	upload_start.value = true;
	// progress callback
	function cbProgress(percent, idx) {
		file_list.value.data[idx].progress = percent + '%'
		file_list.value.data[idx].upload_status = 'progress';

		if(percent == 100) {
			file_list.value.data[idx].upload_status = 'complete'
			file_list.value.data[idx].upload_finish = true
			files_uploaded.value += 1;
			total_files_to_upload.value -= 1;	
			// test if...
		  if(isUploadFinished()) {
				setMessage(1);
			}
		}
	}

	// Inicia os uploads
	upload_started.value = true

	file_list.value.data.forEach((element, index) => { 

	// Excludes already uploaded files then continue
	if (element.upload_finish == false && element.progress == '0%') { 

		let formData = new FormData()
		formData.append('file', element.file);

		// config for Axios 
		const config = {
			cancelToken: element.cancelSource.token,
    	onUploadProgress: function (progressEvent) {
    	  var percentCompleted = Math.round(
    	    (progressEvent.loaded * 100) / progressEvent.total
    	  );
    	  cbProgress(percentCompleted, index);
    	},
    	headers: {
					 "Access-Control-Allow-Origin": "*", 
		   	   "Content-Type": "multipart/form-data",
    	},
  	};

		// call axios() 
		let URL = 'http://localhost:8090/upload'	
		axios.post(URL, formData, config) 
					 .then((res) => {
							// OK Message returned from the server * not used yet *
							console.log('> ' + res.data.data.name + ' '+ res.data.message) 

						})
					 .catch((err) => { 
					  	if (err.response) {
						  	console.log('Error: Some internal server error' )
						  } else 
						  if (err.request) {
							  // console.log('Error: No server response. Server may not be up' )
								setMessage(2);
	  					}else 
							console.log('Error: ' + err) 
					 })
			}
	}) // upload_files blocks end
}
function cancelUpload(file) {
		if (!file.upload_finish) {
			file.cancelSource.cancel('Cncelled upload.');
			file.cancelled = !file.cancelled;
			if( file.cancelled) {
				total_files_to_upload.value -= 1;
			} else {
				total_files_to_upload.value += 1;
			}
			// test if all complete
			if (isUploadFinished()) {
				setMessage(1);
			}
		}
}

function formatBytes(bytes, decimals = 2) {
  if (!bytes) return '0 Bytes'
  const k = 1024
  const dm = decimals < 0 ? 0 : decimals
  const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB']
  const i = Math.floor(Math.log(bytes) / Math.log(k))
  return `${parseFloat((bytes / Math.pow(k, i)).toFixed(dm))} ${sizes[i]}`
}

function file_size_local(fileInput) {
	let size_in_bytes = 0;
	if(fileInput) {
		fileInput.forEach( (member) => {
			size_in_bytes = size_in_bytes + member.size 
		})
	}
	return size_in_bytes;
}
function string_shortner(s) {
	if (s) {
		let max		 	= 35 				
		let len 		= s.length 
		// case:  s < 30 
		if (len>35 ) { 
			return s.slice(0,max)  + '...'
		} else {
			return s
		}
 	}	
}
function isUploadFinished() {

	let file_count = file_list.value.data.length;
	let finish_and_cancelled = countCancelled().length + countFinished().length
	if ( file_count == finish_and_cancelled )	 {
		total_files_to_upload.value = 0 ;
		return true 
	}
	else 
		return false
}


function stateTool(option) {
	if(option == 'sav') {
		localStorage.setItem('files', file_list);
	} else if (option == 'load') {
		
		//(bug) is this a bug ?
		file_list.value = localStorage.getItem('files');
		//return localStorage.getItem('files');
	}
}
function countCancelled() {
	return file_list.value.data.filter((file) => file.cancelled  )
}
function countFinished() {
	return file_list.value.data.filter((file) => file.upload_finish )
}


function removeElement (index, file) {
	// this need a better prompt
	if (confirm("Deletar " + file.filename + " ? ")) {
		file_list.value.data.splice(index,1)

	}

}

